@import url(https://fonts.googleapis.com/css2?family=Asap+Condensed:ital,wght@0,400;0,600;1,400;1,600&display=swap);
body {
    background-color: #eeeeee;
    color: #333333;
    font-family: 'Asap Condensed', sans-serif;
    margin: 0;
    padding: 0;
}

a {
    color: blue;
    text-decoration: underline;
}

h1, h2, h3, p {
    margin: 13pt 0 8pt 0;
    padding: 0;
}

h1 {
    font-size: 200%;
}

h2 {
    font-size: 170%;
}

h3 {
    font-size: 150%;
}

p {
    margin: 5pt 0 3pt 0;
}

a:hover {
    text-decoration: none;
}

.container {
    margin: 0 auto;
    width: 90%;
}

.lead {
    font-size: 120%;
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

.nowrap {
    flex-wrap: nowrap;
}

.col-half {
    padding-right: 1ex;
    width: 50%;
}

button {
    background-color: white;
    border: 1px solid #333333;
    box-shadow: 0 1px 2px silver;
    border-radius: 2px;
    margin: 3pt;
    padding: 3pt 5pt;
}

button.active {
    background-color: blue;
    color: white;
}

button:hover {
    background-color: white;
    border-color: blue;
    box-shadow: 0 1px 5px gray;
    color: blue;
}

.jumbotron {
    background-color: #f6f6f6;
    padding: 1em;
    margin: 1ex 0;
    border-radius: 5pt;
    box-shadow: 0 1px 3px #ccc;
}

nav {
    background-color: #111111;
}

nav .wrapper {
    margin: 0 auto;
    width: 90%;
}

nav ul {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    align-items: center;
    margin: 0;
    padding: 0;
}

nav ul li {
    display: flex;
}

nav ul li a {
    color: white;
    font-size: 130%;
    font-weight: bold;
    display: block;
    margin: 8pt;
    padding: 3pt 5pt;
    text-decoration: none;
}

nav ul li a.misc {
    font-size: 110%;
    font-weight: normal;
}

nav ul li a.logo {
    background: none transparent !important;
    color: white;
    font-size: 200%;
    margin-right: 1em;
}

nav ul li a:hover {
    background-color: white;
    color: blue;
}

nav ul li a.active {
    background-color: blue;
    color: white;
}

.chord {
    background-color: #f6f6f6;
    border: 1px solid #ddd;
    box-shadow: 0 1px 2px #999;
    border-radius: 3pt;
    color: gray;
    display: flex;
    flex-flow: column;
    padding: 1em;
    margin: 1ex;
    -webkit-transition: 200ms;
    transition: 200ms;
}

.chord .title b {
    padding-right: 1ex;
}

.chord .play button {
    margin: 0;
    padding: 2pt;
}

.chord .pitches {
    align-items: stretch;
    align-content: space-between;
    display: flex;
    flex-flow: row;
    flex-grow: 1;
    padding-top: 1ex;
}

.chord .pitches span {
    color: black;
    font-weight: bold;
    font-size: 120%;
    flex-grow: 1;
    padding-right: 3pt;
}

.chord.secondary {
    background-color: #ffffff;
}

.chord.playing {
    background-color: #dddddd;
    border-color: blue;
    -webkit-transition: 200ms;
    transition: 200ms;
}

.chord small {
    padding: 0 1ex;
}

.selector-options-item {
    flex-grow: 1;
}

.selector-options-item label {
    font-size: 150%;
    padding: 0 1em 0 1ex;
}

